const ajax = {
    //reqInterceptors: [],// todo
    resInterceptors: {},
    /*
     * @url - the api url
     * @data - query string data obj
     * @dataType - response data type
     * @return - data , json obj
     */
    get: function (url, data = {}, dataType = "text/json") {
        // promise
        return new Promise((resolve, reject) => {
            // xhr
            var xhr;
            if (global.XMLHttpRequest) {
                xhr = new XMLHttpRequest();
            } else {
                xhr = new ActiveXObject('Microsoft.XMLHTTP');
            }

            let query = [];
            Object.keys(data).forEach(key => {
                query.push(key + '=' + data[key]);
            });
            xhr.open('GET', url + "?" + query.join('&'), true);
            // header
            xhr.setRequestHeader("Accept", "application/json");
            // responseType
            // xhr.responseType = dataType;
            xhr.onreadystatechange = function () {
                if (this.readyState === 4) {
                    if (this.status === 200) {
                        var res = JSON.parse(this.responseText);
                        console.log("[$ajax get]", url, data, res);
                        resolve(ajax.resInterceptors(res));
                    }
                    else {
                        reject({ code: this.status, response: this.response });
                    }
                }
            }
            xhr.send();
        });
    },
    /*
    * @url - the api url
    * @data - querystring data obj
    * @dataType - response data type
    * @return - data default json
    * tips:support get/post , so formdata's format is like querystring
    */
    post: function (url, data = {}, headers = { 'Content-Type': 'application/x-www-form-urlencoded' }, dataType = "text/json") {
        return new Promise((resolve, reject) => {
            // xhr
            var xhr;
            if (global.XMLHttpRequest) {
                xhr = new XMLHttpRequest();
            } else {
                xhr = new ActiveXObject('Microsoft.XMLHTTP');
            }
            xhr.open('POST', url, true);
            // headers
            if (headers) {
                for (var k in headers) {
                    xhr.setRequestHeader(k, headers[k]);
                }
            }
            // responseType
            // xhr.responseType = dataType;
            xhr.onreadystatechange = function () {
                if (this.readyState === 4) {
                    if (this.status === 200) {
                        let res = JSON.parse(this.responseText);
                        console.log("[$ajax post]", url, data, res);
                        resolve(ajax.resInterceptors(res));
                    }
                    else {
                        reject({ code: this.status, response: this.response })
                    }
                }
            }
            // formdata
            let query = [];
            Object.keys(data).forEach(key => {
                query.push(key + '=' + data[key]);
            });
            xhr.send(query.join('&'));
        });
    },
    /*
    * @url - the api url
    * @data - form data obj
    * @dataType - response data type
    * @return - data default json
    * tips:only support post json
    */
    postJson: function (url, data = {}, headers = { 'Content-Type': 'application/x-www-form-urlencoded' }, dataType = "text/json") {
        return new Promise((resolve, reject) => {
            // xhr
            var xhr;
            if (global.XMLHttpRequest) {
                xhr = new XMLHttpRequest();
            } else {
                xhr = new ActiveXObject('Microsoft.XMLHTTP');
            }
            xhr.open('POST', url, true);
            // headers
            if (headers) {
                for (var k in headers) {
                    xhr.setRequestHeader(k, headers[k]);
                }
            }
            // responseType
            // xhr.responseType = dataType;
            xhr.onreadystatechange = function () {
                if (this.readyState === 4) {
                    if (this.status === 200) {
                        let res = JSON.parse(this.responseText);
                        console.log("[$ajax postJson]", url, data, res, this);
                        resolve(ajax.resInterceptors(res))
                    }
                    else {
                        reject({ code: this.status, response: this.response })
                    }
                }
            }
            xhr.send(JSON.stringify(data));
        });
    },
    /*
     * @url - the jsonp api url
     * @data - querystring data obj
     * @server - server callback querystring key
     * @return - data
     */
    jsonp: function (url, data = {}, server = "cb") {
        return new Promise((resolve, reject) => {
            try {
                // random generate jsonp function name
                let client = "jsonp" + Math.ceil(Math.random() * 1000000);
                // query string
                let query = [];
                Object.keys(data).forEach(key => {
                    query.push(key + '=' + data[key]);
                });
                query.push(server + '=' + client);


                global[client] = function (res) {
                    // return response by resolve
                    resolve(res, this);
                    // remove callback function
                    try {
                        delete window[client];
                    } catch (e) {
                        global[client] = undefined;
                    }
                    // remove script
                    let script = document.getElementById(server + "_" + client);
                    document.getElementsByTagName('head')[0].removeChild(script);
                }

                let script = document.createElement('script');
                script.src = url + "?" + query.join('&');
                script.id = server + "_" + client;

                let head = document.getElementsByTagName('head')[0];
                head.appendChild(script);
                console.log("[$ajax jsonp]", url, data, script);
            }
            catch (err) {
                reject(err)
            }
        });
    }
}

export default ajax

