
var _CRM_API = process.env.API_POOL.CRM_API;

var _CRM_ACCESS_API = process.env.API_POOL.CRM_ACCESS_API;

export default {
    mapModuleRoute: function (module) {
        if (module === "CRM") return _CRM_API;
        if (module === "CRM_ACCESS") return _CRM_ACCESS_API;
        return _CRM_API;
    },
    getModuleHost: function (module, url) {
        var apiAddress = "";
        if (url.indexOf('/') == 0) {
            apiAddress = this.mapModuleRoute(module) + url;
        }
        else {
            apiAddress = this.mapModuleRoute(module) + '/' + url;
        }
        return apiAddress;
    }
}



