import Vue from 'vue'
import HighCharts from 'highcharts'
Vue.prototype.$HighCharts = HighCharts
import App from './App'
import router from './router/index'
import {
  Tabs,
  Table,
  TableColumn,
  TabPane,
  Menu,
  MenuItem,
  Row,
  Col,
  Select,
  Option,
  Tag,
  Button,
  Pagination,
  Form,
  FormItem,
  Input,
    Radio,
  RadioGroup,
  Checkbox,
  CheckboxGroup,
  Message,
  Alert,
  Loading,
  Tooltip,
  Progress,
  MessageBox
} from 'element-ui'

Vue.use(Tabs);
Vue.use(Table);
Vue.use(TableColumn);
Vue.use(TabPane);
Vue.use(Menu);
Vue.use(MenuItem);
Vue.use(Row);
Vue.use(Col);
Vue.use(Select);
Vue.use(Option);
Vue.use(Tag);
Vue.use(Button);
Vue.use(Pagination);
Vue.use(Form);
Vue.use(FormItem);
Vue.use(Input);
Vue.use(Radio);
Vue.use(RadioGroup);
Vue.use(Checkbox);
Vue.use(CheckboxGroup);
Vue.use(Loading);
Vue.use(Tooltip);
Vue.use(Progress);
Vue.prototype.$message = Message
Vue.prototype.$alert = MessageBox.alert
Vue.prototype.$confirm = MessageBox.confirm



// [normal] show vue dev log on broswer console
Vue.config.debug = process.env.NODE_ENV == 'development';
//console.log('[Vue.config.debug]', Vue.config.debug);
// [normal] show vue dev log on broswer console
Vue.config.productionTip = false;
// [security] stop modify property's value by vue-dev-tool
Vue.config.devtools = Vue.config.debug;

// import vue validator
import VueValidate from './plug-in/vue-validate/vue-validate.plugin';
Vue.use(VueValidate, { lang: 'zh-cn', delay: 0 });
Vue.prototype.$validator = VueValidate;

// import utils
import utils from './libs/utils'
Vue.prototype.$utils = utils;

import apiHelper from './libs/api-helper'
Vue.prototype.$api = apiHelper;

import ajaxHelper from './libs/ajax-helper'
Vue.prototype.$ajax = ajaxHelper;

// [ext]  ajaxHelper.resInterceptors check the response status

ajaxHelper.resInterceptors = function (res) {
  if (res.code == 1000) {// 1000
    //console.log("[$ajax resInterceptors] data has access", res);
  }
  else if (res.code == 1100) {// 1100 neeed login
    //console.log("[$ajax resInterceptors] redirect to login");
    if(!Vue.config.debug)
      {
        window.location.href = '/index/index?returnUrl='+window.location.pathname;//[ext] ping the php api address
      }
  }
  else if (res.code == 1200) {// 1200 no access
    //console.log("[$ajax resInterceptors] no access");
    next({ name: 'NoAccessPageRouter' });
  }
  return res;
}

// [ext] user menu access check before each routers
router.beforeEach((to, from, next) => {
//  console.log("[vue-router beforeEach] goto", to);
  if (!!to.meta.auth) {// if router is auth page
    ajaxHelper.get(apiHelper.getModuleHost('CRM_ACCESS', "ContractApi/user")).then(
      function (res) {
        if (res.data.code == "1000") {// ok
          let accessMenuList = res.data.data.data.menu;
          //console.log("[vue-router beforeEach] user menu list", accessMenuList);
          if (accessMenuList && accessMenuList.length) {
            for (let index = 0; index < accessMenuList.length; index++) {
            //  console.log("[vue-router beforeEach] access key", index, accessMenuList[index].name, to.meta.accessName);
              if (accessMenuList[index].name == to.meta.accessName) {
              //  console.log("[vue-router beforeEach] menu has access", to);
                next();
                return;// [ext]
              }
            }
          }
        //  console.log("[vue-router beforeEach] no access");
          next({ name: 'NoAccessPageRouter'});
        }
      }
    ).catch(function (err) {
    //  console.error("[vue-router beforeEach] client err", err);
      next({ name: 'ErrorPageRouter' });
    });
  }
  else {// if page is not auth page
    //console.log("[vue-router beforeEach] no auth menu");
    next();
  }
});

router.afterEach((to, from, next) => {
  document.title = to.meta.title ? to.meta.title : 'CRM';
});
//bus
Vue.prototype.$bus=new Vue({})

new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
  data: {
    Bus: new Vue()
  }

  //render: (h) => h(App) // [todo]
})



