
import { defaultConfig } from './vue-validate.plugin.default';

export const show = (message, ...obj) => {
  console.log(`[vue-validate]  ${message}`, ...obj);
};
export const warn = (message, ...obj) => {
  console.warn(`[vue-validate warn] ${message}`, ...obj);
};
export const error = (message, ...obj) => {
  console.error(`[vue-validate error] ${message}`, ...obj);
};


export const isCallable = (func) => typeof func === 'function';

export const merge = (...waitToMergeObjs) => {
  const toObj = Object({});
  waitToMergeObjs.forEach(item => {
    if (item != null) {
      Object.keys(item).forEach(key => {
        toObj[key] = item[key];
      });
    }
  });
  return toObj;
};

export const format = (str, name) => {
  return str.replace(new RegExp("\\{" + 0 + "\\}", "g"), name);
}

export const trim = (str, ident) => {
  return str.replace(/\'/g, '');
}

export const contain = (list, item) => {
  list.forEach(function (p) {
    if (p == item) {
      return true;
    }
    else return false;
  })
}

export const genertateDirective = (directiveKey, options) => {
  options = merge(defaultConfig, options);
  return {
    bind(el, binding, vnode) {
      let validator = vnode.context.$validator;
      if (directiveKey == "validate-key") {
        /* 
         * attachKey: function (id, groups, key, value)
         * @id element id
         * @groups element validate group array
         * @key element v-model bind key
         * @value element v-model bind value
         */
        validator.attachKey(vnode.elm.name, binding.modifiers, binding.expression, binding.value);
        //console.log(">>bind validate-key", vnode.elm.name, binding.modifiers, binding.expression, binding.value);
      }
      else if (directiveKey == "validate-name") {
        /*
         * attachName: function (id, name)
         * @id element id
         * @key element v-model bind value
         */
        validator.attachName(vnode.elm.name, binding.expression);
        //console.log(">>bind validate-name", vnode.elm.name, binding.expression);
      }
      else if (directiveKey == "validate-rule") {
        /* attachRule: function (id, rules)
         * @id element id
         * @key element rule name array
         */
        validator.attachRule(vnode.elm.name, binding.expression);
        //console.log(">>bind validate-rule", vnode.elm.name, binding.expression);
      }
    },
    update(el, binding, vnode) {
      if (directiveKey == "validate-key") {
        let validator = vnode.context.$validator;
        validator.updateValue(vnode.elm.name, binding.value);
      }
    },
    unbind(el, binding, vnode) {
      let validator = vnode.context.$validator;
      validator.detach();
    }
  };
};


