import Vue from 'vue'
import VueRouter from 'vue-router'

// Index/Home
const IndexPageRouter = resolve => require(['../views/index.page'], resolve)

const User = resolve => require(['../components/Info/createUser.page'], resolve)
const UserView = resolve => require(['../components/Info/viewUser.page'], resolve)

const HistoryList = resolve => require(['../components/index/HistoryList'], resolve)
const HistoryDetail = resolve => require(['../components/index/HistoryDetail'], resolve)
// const DataInfo = resolve => require(['../components/index/DataInfo'], resolve)
// const Company = resolve => require(['../components/index/Company'], resolve)

// 管理面板
const MngviewPageRouter = resolve => require(['../views/mngview.page'], resolve)
const AgentData = resolve => require(['../components/mngview/AgentData'], resolve)
const CustomerData = resolve => require(['../components/mngview/CustomerData'], resolve)
const FieldData = resolve => require(['../components/mngview/FieldData'], resolve)

// 管理面板列表
const MnglistPageRouter = resolve => require(['../views/mnglist.page'], resolve)
const Mngdetail = resolve => require(['../components/mnglist/mngdetail'], resolve)
const Mngversion = resolve => require(['../components/mnglist/mngversion'], resolve)

// 404
const NoFoundPagetRouter = resolve => require(['../views/404.page'], resolve)
// no access
const NoAccessPageRouter = resolve => require(['../views/no-access.page'], resolve)
// error
const ErrorPageRouter = resolve => require(['../views/error.page'], resolve)

// const contractDetailPagerRouter = resolve => require(['../views/contract-detail.page'], resolve)



Vue.use(VueRouter)

var router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/foreground/index',
      name: 'IndexPageRouter',
      component: IndexPageRouter,
      alias: '/',
      meta: {
        title: 'CRM',
        auth: false,
        accessName: 'CRM'
      },
      children: [
        {
          path: 'user/:mobile?/:company_id?/:customer_id?',
          name:"user",
          component: User
        },
        {
          path: 'userview/:mobile?/:company_id?/:customer_id?',
          name:"userview",
          component: UserView
        },
        {
          path: 'history',
          name:"history",
          component: HistoryList
        },
        {
          path: 'historyDetail',
          name: "historyDetail",
          component: HistoryDetail
        }
			]
		},
    {
      path: '/foreground/mngview',
      name: 'mngview',
      component: MngviewPageRouter,
      meta: {
        title: 'CRM',
        auth: false,
        accessName: 'CRM'
      },
      children: [
        {
          path: 'agentData',
          name:"agentData",
          component: AgentData
        },
        {
          path: 'customerData',
          name:"customerData",
          component: CustomerData
        },{
          path: 'fieldData',
          name:"fieldData",
          component: FieldData
        }
      ]

    },
    {
      path: '/foreground/mnglist',
      name: 'mnglist',
      component: MnglistPageRouter,
      meta: {
        title: 'CRM',
        auth: false,
        accessName: 'CRM'
      },
      children: [
        {
          path: 'mngdetail/:id',
          name:"mngdetail",
          component: Mngdetail,
          children:[
            {
              path: 'mngversion',
              name:"mngversion",
              component: Mngversion
            }
          ]
        }
      ]

    },
		{
			path: '*',
			name: 'NoFoundPagetRouter',
			component: NoFoundPagetRouter,
			meta: {
				title: 'CRM-404',
				auth: false
			}
		},
		{
			path: '/foreground/NoAccess',
			name: 'NoAccessPageRouter',
			component: NoAccessPageRouter,
			meta: {
				title: 'CRM-无权限',
				auth: false
			}
		},
		{
			path: '/foreground/Error',
			name: 'ErrorPageRouter',
			component: ErrorPageRouter,
			meta: {
				title: 'CRM-内部错误',
				auth: false
			}
		},


		// {
		//   path: '/ContractAduit/Contract',
		//   name: 'contractDetailPagerRouter',
		//   component: contractDetailPagerRouter,
		//   children: [
		//     {
		//       path: 'contractInfo/:id/:supplier_id?/:contract_number?',
		//       name: 'contractInfo',
		//       component: contractInfoPagerRouter
		//     },
		//     {
		//       path: 'contractUploadHis/:id/:supplier_id?/:contract_number?',
		//       name: 'contractUploadHis',
		//       component: contractUploadHisPagerRouter
		//     },
		//     {
		//       path: 'billInfo/:id/:supplier_id?/:contract_number?',
		//       name: 'billInfo',
		//       component: billInfoPagerRouter
		//     },
		//     {
		//       path: 'moneyInfo/:id/:supplier_id?/:contract_number?',
		//       name: 'moneyInfo',
		//       component: moneyInfoPagerRouter
		//     }
		//   ]
		// },

	]
});

export default router;



