import { defaultConfig, defaultRule } from './vue-validate.plugin.default';
import { show, warn, error, merge, format, trim, genertateDirective } from './vue-validate.plugin.utils';//, , makeDirective

export default {
  name: 'vue-validate',
  VueValidate: {
    flag: false,
    config: defaultConfig,
    validateArray: [],
    validateRules: defaultRule
  },
  install: function (Vue, options) {
    // once install
    if (this.VueValidate.flag) {
      error('already installed , Vue.use(VueValidate) should only be called once.');
    }
    else {
      this.VueValidate.flag = true;
      this.VueValidate.config = merge(defaultConfig, options);
      //show('install VueValidate scuess.');
      //show("options|VueValidate.defaultConfig|VueValidate.config", options, defaultConfig, this.VueValidate.config);
    }
    // inject directive
    Vue.directive("validate-key", genertateDirective("validate-key", this.VueValidate.config));
    Vue.directive("validate-name", genertateDirective("validate-name", this.VueValidate.config));// add new
    Vue.directive("validate-rule", genertateDirective("validate-rule", this.VueValidate.config));
    Vue.directive("validate-remind", genertateDirective("validate-remind", this.VueValidate.config));
  },
  extentRule: function (rule, fn, errMsgFormat) {
    let self = this;
    self.validateRules[rule] = { ruleExecute: fn, errMsgFormat: errMsgFormat }
  },
  /* 
   * attachKey: function (id, groups, key, value)
   * @id element id
   * @groups element validate group array
   * @key element v-model bind key
   * @value element v-model bind value
   */
  attachKey: function (id, groups, key, value) {
    let self = this;
    let _groups = [];
    Object.keys(groups).forEach(key => {
      _groups.push(key);
    });
    self.VueValidate.validateArray.push({
      id: id,
      groups: _groups,
      key: key,
      name: key,
      rules: [],
      value: value
    });
  },
  /*
   * attachName: function (id, name)
   * @id element id
   * @key element v-model bind value
   */
  attachName: function (id, name) {
    let self = this;
    for (let i = 0; i < self.VueValidate.validateArray.length; i++) {
      var item = self.VueValidate.validateArray[i];
      if (item.id == id) {
        self.VueValidate.validateArray[i].name = trim(name);
      }
    }
  },
  /* attachRule: function (id, rules)
   * @id element id
   * @key element rule name array
   */
  attachRule: function (id, rules) {
    let self = this;

    for (let i = 0; i < self.VueValidate.validateArray.length; i++) {
      var item = self.VueValidate.validateArray[i];
      if (item.id == id) {
        self.VueValidate.validateArray[i].rules = trim(rules).split('|');
      }
    }
  },
  updateValue: function (id, newValue) {
    let self = this;

    for (let i = 0; i < self.VueValidate.validateArray.length; i++) {
      var currentValidateItem = self.VueValidate.validateArray[i];
      if (currentValidateItem.id == id) {
        self.VueValidate.validateArray[i].value = newValue;
      }
    }
  },
  verify: function (group) {
    let self = this;
    let globalMsg = [];
    let groupMsg = [];

    for (let i = 0; i < self.VueValidate.validateArray.length; i++) {
      let currentValidateItem = self.VueValidate.validateArray[i];
      for (let j = 0; j < currentValidateItem.rules.length; j++) {
        let currentValidateRule = currentValidateItem.rules[j];

        if (!self.VueValidate.validateRules[currentValidateRule].ruleExecute(currentValidateItem.value)) {
          let errMsg = format(self.VueValidate.validateRules[currentValidateRule].errMsgFormat, currentValidateItem.name);
          if (group && contain(currentValidateItem.groups, group)) {
            groupMsg.push(errMsg);
          }
          globalMsg.push(errMsg);
        }
      }
    }
    return group ? groupMsg : globalMsg;
  },
  singleVerify: function (value, rules) {
    let self = this;
    for (let i = 0; i < rules.length; i++) {
      let currentRule = rules[i];
      if (!self.VueValidate.validateRules[currentRule].ruleExecute(value)) {
        return true;
      }
    }
    return false;
  },
  detach: function () {
    this.VueValidate.validateArray = [];
  }
}





