
export const defaultConfig = {
  mode: 'dev', // dev or prod
  lang: 'en',
  delay: 0
};


export const defaultRule = {
  required: {
    name: "required",
    ruleExecute: function (val) {
      return !((undefined == val) || ("-" == val) || ("" == val) || (" " == val) || (0 == val) || !val );
    },
    errMsgFormat: '{0}不能为空'
  },
  email: {
    name: "email",
    ruleExecute: function (val) {
      return /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/.test(String(val))
    },
    errMsgFormat: '{0}格式错误'
  },
  mobile: {
    name: "mobile",
    ruleExecute: function (val) {
      return /^1[0-9]{10}$/.test(String(val))
    },
    errMsgFormat: '{0}格式错误'
  },
  numb: {
    name: "numb",
    ruleExecute: function (val) {
      return /^[\d\-\s]+$/.test(String(val))
    },
    errMsgFormat: '{0}格式错误'
  }
}


